
@media All and (min-width: 1024px) and (max-width: 3000px){
    .home{
        /* border: 1px solid red; */
        background-color: #1f1f1f;
        color: white;
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-around;
        
    }
    
    .home>div:nth-child(1){
        width: 50%;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        /* border: 1px solid red; */
    
    }
    .home>div:nth-child(2){
      
        /* width: 70%; */
        font-weight: bold;
        text-align: left;
        padding: 10% 10%;
        /* border: 1px solid blue; */
    }
    .home>div:nth-child(1)>img{
       /* border-radius: 5px; */
        width: 60%;
        box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    }
    .home>div:nth-child(2)>p{
        font-weight: 400;
    }
    h1{
        color: 
        
    #ed0331;
    }
    .info>h3{
        color: 
        
    #ed0331; 
    }
    .info>a>button{
        padding: 7px 9px;
        border: 1px solid #1f1f1f ;
        border-radius: 50%;
       /* transition-duration: 100ms; */
        color: 
        white;
        background-color: #ed0331;
    }
    .info>a{
        margin-right: 5%;
    }
    
}
