*{
    font-family: 'Rowdies', cursive;
   
  
}
.Navbar{
    /* border: 1px solid red; */
    width: 100%;
 
}
.Sections{
    background-color: #1f1f1f;
}
.navbar{
    border-bottom: 1px solid black;
    width: 100%;
    height: 60px;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
   
 
    
}
.navbar>div{
    margin-left: 2%;
    margin-right: 2%;
    /* color: #fa437c; */
    
    
}
.navbar>div:last-child{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    /* background-color: #eaeded; */
    /* background-color: #E0FBFC; */
    /* border: 1px solid red; */
}
.nav-item>a{
    text-decoration: none;
    color: #ed0331 ;
    /* margin-bottom: 3px; */
}
.resumebtn{
    padding: 5px 10px;
    border: 1px solid ;
    border-radius: 5px;
    background-color: white;
    color: #ed0331;
    transition-duration: 1s;
}
.resumebtn:hover{
 
        padding: 5px 20px;
    
}
.collapse{
    background-color: #f8f9fa;
}


@media All and (min-width: 0px) and (max-width: 480px){
    .Navbar{
        margin-top: -1px;
        width: 100%;
    }
    .navbar{
        border-bottom: 1px solid white;
        width: 100%;
        display: flex;
        position: fixed;
        justify-content: center;
        align-items: center;
        margin-bottom: 190px;
        /* margin-top: -30px; */
        /* background-color: #E0FBFC; */
        
    }
    .navbar>div{
        margin-left: 2%; 
         margin-right: 2%;
        /* color: #fa437c; */
      
    }
    .navbar>div:last-child{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        
        /* background-color: #E0FBFC; */
        /* border: 1px solid red; */
    }
    .collapse{
        background-color: #f8f9fa;
    }

}
