.aboutme{
    padding-top: 10px;
    background-color: #1f1f1f;
    color: white;
    width: 100%;
    margin: auto;
    text-align: center;
    margin-top: 10px;
    /* border: 1px solid red; */
    /* margin-bottom: 10px; */
    /* background-color: antiquewhite; */
}
.github{
    /* border: 1px solid red; */
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.github>img{
    margin-top: 20px;
    margin-bottom: 20px;
}
.edu{
    width: 70%;
    text-align: left;
    margin: auto;
}
.edu>p:nth-child(1){
    color: #ed0331;
    font-size: 20px;
}
@media All and (min-width: 0px) and (max-width: 480px){
    .github>img{
        width: 100%;
        margin-bottom: 20px;
    }
    .edu{
        width: 90%;
        text-align: left;
        margin: auto;
    }
}
@media All and (min-width: 481px) and (max-width: 768px){
    .github>img{
        width: 100%;
        margin-bottom: 20px;
    }
    .edu{
        width: 90%;
        text-align: left;
        margin: auto;
    }
}
