.contact{
    text-align: center;
    background-color: #1f1f1f;
    color: white;
    /* margin-top: 10px; */
    padding-top: 20px;
    padding-bottom: 10px;
    border-top: 1px solid white;
}
.contact>div{
    /* border: 1px solid black; */
    padding-top: 10px;
    padding-bottom: 10px;
    width: 40%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
   
    
}
.contact_div>div{
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
    padding: 7px 9px;
    background-color:#ed0331 ;
    border-radius: 50%;
    text-decoration: none;
    margin-left: 5px;
    margin-right: 5px;
    transition-duration: 300ms;
    margin-bottom: 10px;
}
.contact_div>div>a{
    text-decoration: none;
    color: white;
    /* margin-left: 1%; */
}
.contact_div>div:hover{
   background-color: black;
   color:#ed0331 ;
   margin-bottom: 30px;
   cursor: pointer;
}

.phone:hover{
    cursor: pointer;
}
.phoneIcon{
    color: #ed0331;
    transition-duration: 2s;
}
.phoneIcon:hover{
    cursor: pointer;
    margin-right: 10px;
}
