.project{
    color: white;
    text-align: center;
    margin-top: 100px;
    padding-top: 20px;
    /* border: 1px solid red; */
   
}
.project_div{
   
    width: 90%;
    margin: auto;
    border: 2px solid white;
    /* padding: 10px 20px; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.project_div:hover{
   border: 2px solid 
   #ed0331;
}
.project_div>div:nth-child(1){
    width: 60%;
    /* border: 1px solid red; */
}
.project_div>div:nth-child(2){
    width: 40%;
    padding: 10px;
    text-align: left;
    margin-left: 20px;
    /* border: 1px solid red; */
}
.project_div>div>img{
    width: 70%;
    
    margin-left: 15%;
}
.proinfo{
    color: white;
   
}
.proinfo>h4{
    color:#ed0331 ;
}
.proinfo>div{

    /* border: 1px solid red */
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    
}
.proinfo>div>a{
  
    text-decoration: none;
    color: #ed0331;
}
.proinfo>div>a{
    border: 1px solid white;
    border-radius: 3%;
    padding: 3px 7px;
    background-color: white;
}
@media All and (min-width: 0px) and (max-width: 480px){
    .project{
        text-align: center;
        margin-top: 100px;
    }
    .project_div{
        width: 92%;
        margin: auto;
      
        display: flex;
        flex-direction: column;
     
        align-items: center;
        margin-bottom: 20px;
    }
    .project_div>div>img{
       border: 1px solid red;
       width: 100%;
       margin: auto;
       margin-top: 5px;
    }
    .project_div>div:nth-child(2){
       /* border: 1px solid red; */
       width: 90%;
       margin: auto;
       margin-bottom: 3px;
    }
    .project_div:hover{

        box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
      }
      .proinfo>div{
        /* border: 1px solid red; */
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }
    .proinfo>div>a{
        border: 1px solid white;
        border-radius: 3%;
        padding: 3px 7px;
        background-color: white;
    }
}
@media All and (min-width: 481px) and (max-width: 768px){
    .project{
        text-align: center;
        margin-top: 100px;
    }
    .project_div{
        width: 95%;
        margin: auto;
     
        display: flex;
        flex-direction: column;

        align-items: center;
        margin-bottom: 20px;
    }
    .project_div>div:nth-child(1){
        width: 80%;
        margin: auto;
        padding-top: 10px;
        /* border: 1px solid red; */
    }
    .project_div>div:nth-child(2){
        width: 80%;
        margin: auto;
        text-align: left;
    
  
    }
    .project_div:hover{

        box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
      }
      
      .proinfo>div{
        /* border: 1px solid red; */
        width: 60%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }
    .proinfo>div>a{
        border: 1px solid white;
        border-radius: 3%;
        padding: 3px 7px;
        background-color: white;
    }
}